<template>
  <div class="audit_create_case">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr">
    </listSearch>
    <div class="content">
      <div class="buttons">
        <Button type="primary" @click="handleSubmit(true)">批量通过</Button>
      </div>
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          @on-select="selectOne"
          @on-select-cancel="selectCancel"
          @on-select-all="selectAll"
          @on-select-all-cancel="selectAllCancel"
          :columns="columns"
          :data="tableData"
          show-sizer
          show-elevator
          show-total>
          <template slot-scope="{ row }" slot="proxyName">
            <span>{{ row.proxyName }}</span>
          </template>
          <template slot-scope="{ row }" slot="state">
            <span>{{ caseStatus[row.state] }}</span>
          </template>
          <template slot-scope="{ row }" slot="action">
            <div>
                <a href="javascript:;" style="margin-right:20px" @click="handleSubmit(false,row)" title="通过">
                <img width="26" height="26" src="../../../assets/images/resolve.png" alt />
                </a>
                <a href="javascript:;" @click="handleBack(row)" title="立案驳回">
                  <img width="26" height="26" src="../../../assets/images/reject.png" alt />
                </a>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page
          :current="currentPage"
          :total="total"
          :page-size="pageSize"
          :page-size-opts="pageOpts"
          @on-change="changeCurrentPage"
          @on-page-size-change="changeSize"
          show-sizer
          show-elevator
          show-total
        />
      </div>
    </div>
    <!-- 弹窗部分 -->
        <!-- 驳回原因 -->
    <Modal v-model="back" :footer-hide="true" title="驳回" ok-text="驳回">
      <Form ref="backFormValidate" :model="backForm" :rules="backFormRuleValidate" :label-width="0">
        <div style="margin-bottom:10px">请输入驳回原因：</div>
        <FormItem label prop="backInput">
          <Input
            v-model="backForm.backInput"
            :maxlength="100"
            show-word-limit
            type="textarea"
            placeholder="请输入原因"
            style="width:100%"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="handleCancel">取消</Button>
        <Button type="primary" @click="backSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import {listMixin} from '../listMixin'
const columns = [
        {
          type: 'selection',
          width: 100,
          align: 'center',
        },
        {
          title: '案件编号',
          key: 'id',
          align: "center",
          width:200,
        },
        {
          title: '案件类型',
          key: 'caseTypeName',
          align: "center"
        },
        {
          title: '申请人',
          key: 'applicantName',
          align: "center"
        },
        {
          title: '代理人',
          key: 'proxyName',
          slot:"proxyName",
          align: "center"
        },
        {
          title: '被申请人',
           key: "respondentName",
          align: "center"
        },
        {
          title: '案件状态',
          key: 'state',
          slot:"state",
          align: "center"
        },
        {
          title: '受理时间',
          key: 'acceptTime',
           align: "center",
        },
        {
          title: '操作',
          slot: 'action'
        }
      ]
import {mapActions} from 'vuex'
export default {
  name: 'audit_create_case',
  components: { listSearch },
  mixins: [listMixin],
  data () {
    return {
      popupShow: false,
      popupType: 4,
      popupTypeObj: {
        1: {type:1, title: '通过'},
        2: {type:2, title: '驳回'},
        3: {type:3, title: '通过'},
        4: {type:4, title: '驳回'},
      },
      acceptList: ['hahah',2],
      rejectList: [1,2],
      rejectReason: null,
      disputeType: null,
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      searchArr: [
        {type: 'input', value: 'companyName', label: '申请人', placeholder: '请输入企业名称'},
        {type: 'input', value: 'companyName', label: '被申请人', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '注册名称',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        },
        {type: 'select', value: 'status', label: '案件类型',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        }
      ],
      columns: columns,
      back:false,
      backInput:'',
      backRowId:[], //驳回ids
       backForm: { backInput: "" },
      backFormRuleValidate: {
        backInput: [
          { required: true, message: "请输入原因", trigger: "change" }
        ]
      },
    }
  },
  mounted () {
  },
  methods: {
    ...mapActions({
      getRequsetList: "createCase/getRequsetList",
      filingCaseOneOrAll:"createCase/filingCaseOneOrAll", // 立案单个/批量
      filingCaseBack:"createCase/filingCaseBack"
    }),
    // 搜索
    goSearch () {
      console.log(this.searchFormData)
    },
    // value true 批量操作 false 单个操作
   async handleSubmit(value,row) {
      let length = this.selectedData.length;
       if (value &&length === 0) {
          this.$Message.warning("请至少选择一条案件信息");
          return;
      }

      this.$Modal.confirm({
          title: "操作",
          content:
            `<div style="color:#3B4453;text-align:center;font-size:18px">您确定要${value?'批量通过'+ length+'条':'提交'}审核吗?</div>`,
          onOk: async() => {
            let params= {   
                  actionIdentifer:"UPCASE_APPROVE_PASS", //操作标识
              }
              if(value){
                // 批量 ids
                params.caseIds= JSON.stringify(this.selectedData)
              } else {
                // 单个
                params.caseIds=JSON.stringify(new Array(row.id))
              }
              console.log('参数：', params)
              
          const result = await  this.filingCaseOneOrAll(params);
             console.log(result);
            if (result.data.flag) {
                 this.$Message.success("操作成功");
                  //  重置
                  
                  this.getList()
                } else {
                  this.$Message.error(result.data.message);
                }
         
            // 单个
            
            
          },
          onCancel: () => {
            console.log('取消了', this.tableData)
            this.selectedData=[]
          }
        });
    },
    //驳回
    handleBack(row){
      this.backForm.backInput='' // 重置
     this.backRowId=new Array(row.id)
     this.back=true
    },
    async backSubmit(){
     this.$refs["backFormValidate"].validate(async valid => {
        console.log(this.backForm);
        
        if (valid) {
          let params= {   
                caseIds: JSON.stringify(this.backRowId),//案件id
                actionIdentifer:"UPCASE_APPROVE _REJECT", //操作标识
                reason:this.backForm.backInput
                }
                console.log(params, '驳回参数')
                
          const result = await  this.filingCaseBack(params);
             console.log(result);
            if (result.data.flag) {
                 this.$Message.success("操作成功");
                  //  重置
                  this.handleCancel();
                  this.getList()
                  
                } else {
                  this.$Message.error(result.data.message);
                }
          // this.isShowDate=false
        } else {
          // this.$Message.error("Fail!");
        }
      });
          
    },
    handleCancel(){
      this.back=false
      this.backRowId=[] // 重置
    },
    async getList() {
      let params = {
        menuIdentifer: "APPROVE_UPCASE_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter = {
        //筛选条件
        applicantName: "", //申请人关键字
        respondentName: "", //被申请人关键字
        userId: "", //注册名称的用户id
        caseTypeId: "", //案件类型id
        createTimeBegin: "", //提交的开始时间
        createTimeEnd: "" //提交的结束时间
      };

      params = { ...params, filter: JSON.stringify(filter) };
      // console.log("参数", params);
      const res = await this.getRequsetList(params);

      if (res.data.flag) {
        // console.log(res.data.data);
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
      }
    }
  }
}
</script>
<style lang="less">
.audit_create_case {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.accept_content {
  text-align: center;
}
</style>
<style lang="less">
@import url('../../../style/iview_table.less');
</style>